	module.exports = {"data":{"markdownRemark":{"html":"<h2>Version Control System</h2>\n<p>Version control systems (VCS), also called Source Code Management (SCM), are tools used to track changes on files, manage version and ease collaborative file editing.\nThere are mainly 2 types of VCS:</p>\n<ul>\n<li>Centralized Version Control System\nWhere a central repository is authoritative. The associate architecture is client/server.\nThe first VCS (CVS, SVN…) were Centralized Version Control System.</li>\n<li>Distributed Version Control System\nWhere multiple repository exchange modification. The associate architecture is mostly peer to peer, but one repo can be declared as authoritative.\nThe most used modern VCS (Git, Mercurial…) are Distributed Version Control System.</li>\n</ul>\n<h3>Why use it?</h3>\n<ul>\n<li><strong>Changes history</strong> - VCS enable the user to browse and search all the changes which are automatically recorded with useful information (date, author…) and</li>\n<li><strong>Versions/tags</strong> - The user can search/retrieve specific state of the files that have been labeled with tags and version names</li>\n<li><strong>Branching/Merging</strong> - Distributed Version Control System make it easy to maintain parallel branch of files and to merge them partially or totally when needed.</li>\n<li><strong>Atomic operations</strong> - All modern VCS provide atomic operations: All modifications are guaranteed to succeed or fail as whole which ensure that the files are always in a consistent state.</li>\n</ul>\n<h3>Most popular Version Control System</h3>\n<ul>\n<li>Git</li>\n</ul>\n<p><em>Git</em> is a Distributed Version Control System and probably the most used VCS used nowadays with <em>Mercurial</em>.</p>\n<ul>\n<li>Mercurial</li>\n</ul>\n<p><em>Mercurial</em> is a Distributed Version Control System and probably the most used VCS used nowadays with <em>Git</em>.</p>\n<ul>\n<li>CVS</li>\n</ul>\n<p><em>CVS</em> is an old SCM which was proeminent before <em>SVN</em> widespread.\n<em>CVS</em> and <em>SVN</em> are now deprecated in favor of Distributed Version Control System like <em>Git</em> and <em>Mercurial</em>.</p>\n<ul>\n<li>SVN/Subversion</li>\n</ul>\n<p><em>SVM</em> is an old SCM that succeeded <em>CVS</em>.\nEventually <em>SVN</em> was deprecated by the wide adoption of Distributed Version Control System like <em>Git</em> and <em>Mercurial</em></p>\n<h3>More Information:</h3>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='http://savannah.nongnu.org/projects/cvs' target='_blank'>CVS</a></p>\n<p><a href='https://git-scm.com/' target='_blank'>Git</a></p>\n<p><a href='https://www.mercurial-scm.org/' target='_blank'>Mercurial</a></p>\n<p><a href='http://subversion.tigris.org/' target='_blank'>SVN</a></p>\n<p><a href='https://en.wikipedia.org/wiki/Version_control' target='_blank'>Version Control on Wikipedia</a></p>","fields":{"slug":"/software-engineering/version-control-system/"},"frontmatter":{"title":"Version Control System"}}},"pathContext":{"slug":"/software-engineering/version-control-system/"}}

